.p-movie {
  padding: 90px 0;
  text-align: center;

  &__heading {
    margin-bottom: 20px;
  }

  &__title {
    font-family: "ShinGo";
    font-weight: 600;
    font-size: 35px;
    line-height: 1.3;
    color: #4e8465;
  }

  &__subtitle {
    @extend .p-movie__title;
    margin-left: 5px;
    font-weight: 300;
    font-size: 14px;
    line-height: 1.5;
  }

  &__about {
    display: inline-block;
    margin-bottom: 16px;
    font-family: "ShinGo";
    font-weight: 300;
    font-size: 12px;
    color: #000;
  }

  &__concept {
    margin: 43px 0;
    display: flex;
    row-gap: 13px;
    flex-direction: column;
    align-items: center;

    &-inner {
      margin-top: 11px;
      width: 330px;
      height: 216px;
      background-color: #5c5c5c;
    }
  }

  &__busyocho {
    margin: 43px 0 90px;
    display: flex;
    row-gap: 13px;
    flex-direction: column;
    align-items: center;

    &-inner {
      margin-top: 11px;
      width: 330px;
      height: 216px;
      background-color: #5c5c5c;
    }
  }
}
